/* ==========================================================================
5. Counterup css start
========================================================================== */
.counterup {
    text-align: center;
    position: relative;
    z-index: 1;

    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.85);
        z-index: -1;
    }

    i {
        color: #ffb100;
        font-size: 40px;
    }

    span {
        display: block;
        color: #fff;
        font-size: 24px;
        font-weight: 400;
        line-height: 1.75em;
        padding-top: 10px;
    }

    p {
        font-size: 16px;
        font-weight: 400;
        color: #999;
        text-transform: capitalize;
    }
}







/* ==========================================================================
5. Counterup css end
========================================================================== */